@import url(https://fonts.googleapis.com/css2?family=Raleway:wght@800;900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

section {
  height: 100vh;
  width: 100vw;
  text-align: center;
  overflow: hidden;
}

.slide-1 {
  /* background-image: linear-gradient( 134.6deg,  rgba(201,37,107,1) 15.4%, rgba(116,16,124,1) 74.7% ); */
  background-image: radial-gradient(circle farthest-corner at 83.7% 4.3%, rgba(173, 0, 171, 1) 0%, rgba(15, 51, 92, 1) 90%);
}

.slide-1-wave-container {
  position: absolute;
  left: -2px;
  width: 102%;
  top: -22vw;
}

.slide-1-wave {
  box-shadow: 0px -9px 43px 0px rgba(0, 0, 0, 0.63);
  -webkit-box-shadow: 0px -9px 43px 0px rgba(0, 0, 0, 0.63);
  -moz-box-shadow: 0px -9px 43px 0px rgba(0, 0, 0, 0.63);
}

@-webkit-keyframes fadeInAnimation {
  0% {
    opacity: 0;
    -webkit-transform: translateY(-10%) scale(.9);
            transform: translateY(-10%) scale(.9);
  }

  20% {
    opacity: 0;
    -webkit-transform: translateY(-2%) scale(.9);
            transform: translateY(-2%) scale(.9);
  }

  100% {
    opacity: 1;
    -webkit-transform: translateY(0) scale(1);
            transform: translateY(0) scale(1);
  }
}

@keyframes fadeInAnimation {
  0% {
    opacity: 0;
    -webkit-transform: translateY(-10%) scale(.9);
            transform: translateY(-10%) scale(.9);
  }

  20% {
    opacity: 0;
    -webkit-transform: translateY(-2%) scale(.9);
            transform: translateY(-2%) scale(.9);
  }

  100% {
    opacity: 1;
    -webkit-transform: translateY(0) scale(1);
            transform: translateY(0) scale(1);
  }
}

.main-title {
  color: white;
  font-weight: 900;
  font-size: 82px;
  text-transform: uppercase;
  vertical-align: middle;
  line-height: 70vh;
  font-family: 'Raleway', sans-serif;

  -webkit-animation: fadeInAnimation cubic-bezier(.29, .81, .48, 1.03) 2s;

          animation: fadeInAnimation cubic-bezier(.29, .81, .48, 1.03) 2s;
  -webkit-animation-iteration-count: 1;
          animation-iteration-count: 1;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}


.slide-2 {
  background-color: white;
}
